import React from "react";
import { Triangle } from "react-loader-spinner";
const Loading = () => {
  return (
    <div className="bg-primary  loading dark:bg-bg-dark flex h-[100vh] w-100 items-center justify-center">
      <Triangle
        visible={true}
        height="80"
        width="80"
        color="#F24432"
        ariaLabel="triangle-loading"
        wrapperStyle={{}}
        wrapperClass=""
      />
    </div>
  );
};

export default Loading;
