import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Modal, Input, Form, message, FloatButton } from "antd";
import TextArea from "antd/es/input/TextArea";
import axios from "axios";
import { PlusOutlined } from '@ant-design/icons';

import { ReloadTrue, hideLoading, showLoading } from "../redux/rootSlice";

const AdminExperience = () => {
  const { portfolioData } = useSelector((state) => state.root);
  const { experiences } = portfolioData; // Use optional chaining
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [isModalEdit, setIsModalEdit] = useState(false);
  const [selectItemEdit, setselectItemEdit] = useState(null);
  const dispatch = useDispatch();
  const [form] = Form.useForm();

  useEffect(() => {
    if (isModalEdit) {
      form.setFieldsValue({
        title: selectItemEdit.title,
        company: selectItemEdit.company,
        desc: selectItemEdit.desc,
        start_year: selectItemEdit.start_year,
        end_year: selectItemEdit.end_year,

      });
    }
  }, [isModalEdit, selectItemEdit, form]);

  const showModal = () => {
    setIsModalOpen(true);
    if (!isModalEdit) {
      form.resetFields();
    }
  };

  const handleOk = () => {
    setIsModalOpen(false);
    setIsModalEdit(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
    setIsModalEdit(false);
    form.resetFields(['title', 'company', 'desc', 'start_year', 'end_year']);
  };




  const onFinish = async (values) => {
    try {
      setIsModalEdit(false);
      dispatch(showLoading());
      handleOk();
      let response;

      if (isModalEdit) {
        response = await axios.post(
          `https://backendz182.up.railway.app/api/portfolio/update-experience/${selectItemEdit?._id}`,
          values
        );
      } else {
        response = await axios.post("https://backendz182.up.railway.app/api/portfolio/add-experience", values);
      }



      dispatch(hideLoading());

      if (response.data.success) {
        message.success(response.data.message);
        dispatch(ReloadTrue());
        form.resetFields();
      } else {
        message.error(response.data.message);
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };

  const onDelete = async (id) => {
    try {
      const isConfirmed = window.confirm(
        "Are you sure you want to delete this Experience?"
      );
      if (isConfirmed) {
        dispatch(showLoading());
        const response = await axios.delete(
          `api/portfolio/delete-experience/${id}`
        );
        dispatch(hideLoading());

        if (response.data.success) {
          message.success(response.data.message);
          // Dispatch the action after a successful API call
          dispatch(ReloadTrue());
          // Clear the selected item after successful deletion
          setselectItemEdit(null);
        } else {
          message.error(response.data.message);
        }
      }
    } catch (error) {
      console.error("Error:", error);
    }
  };


  return (
    <>
      <div>
        <div className="flex  items-center justify-between">
          <p className="text-xl font-bold">Add Experiences</p>
          <button
            type="button"
            onClick={showModal}
            className="button bg-primary dark:bg-bg-dark p-3 font-medium text-white rounded hover:bg-secondary  hover:font-semibold ease-in-out">
            Add Experience &nbsp;
            <PlusOutlined />
          </button>
        </div>
        <div className="flex gap-4 justify-around flex-wrap  py-10 sm:flex-col md:flex-col ">
          {experiences.slice()
            .reverse()
            .map((experience) => {

              return (
                <div
                  className="cardExperience bg-slate-200  px-8 py-10 rounded flex flex-col gap-1 justify-center sm:w-96"
                  key={experience._id}
                >
                  <h1 className="text-primary font-bold text-xl">{experience.title}</h1>
                  <p className="text-primary font-bold ">{experience.company}</p>
                  <p className=" font-semibold">
                    {experience.start_year} - {experience.end_year}{" "}
                  </p>
                  <p className=" desc">{experience.desc}</p>

                  <div className="flex gap-3 justify-end mt-3">
                    <button className="button border py-2 px-4 rounded border-outline border-primary bg-primary/90  dark:bg-bg-dark/90 text-tertiary font-medium hover:bg-primary hover:border-primary hover:text-tertiary  hover:font-semibold ease-in-out"
                      onClick={() => {
                        setselectItemEdit(experience);
                        setIsModalEdit(true);
                        showModal();
                      }}
                    >
                      Edit
                    </button>

                    <button className="button border py-2 px-4 rounded border-outline border-primary font-medium hover:bg-red-700 hover:border-red-700 hover:text-tertiary ease-in-out"
                      onClick={() => {
                        onDelete(experience._id);

                      }}
                    >
                      Delete
                    </button>
                  </div>
                </div>
              );
            }
            )}
        </div>
      </div>
      <div>
        <Modal
          title={isModalEdit ? "Edit Experence" : "Add Experence"}
          footer={null}
          style={{ width: 750 }}
          open={isModalOpen}
          onOk={handleOk}
          onFinish={onFinish}
          onCancel={handleCancel}>
          <Form
            onFinish={onFinish}
            layout="vertical"
            form={form}
            initialValues={isModalEdit ? {} : null}>
            <Form.Item
              label="Title"
              name="title"
              className="font-semibold"
              rules={[{ required: true, message: "Please enter the title." }]}>
              <Input className=" h-[40px]" placeholder="Enter Title" />
            </Form.Item>
            <Form.Item
              label="Comapny"
              name="company"
              className="font-semibold"
              rules={[{ required: true, message: "Please enter the Comppany Name." }]}>
              <Input className=" h-[40px]" placeholder="Enter Company Name" />
            </Form.Item>
            <Form.Item
              label="Description"
              name="desc"
              className="font-semibold"
              rules={[
                { required: true, message: "Please enter the description." },
              ]}>
              <TextArea
                rows={3}
                className="  h-[40px]"
                placeholder="Enter Description"
              />
            </Form.Item>

            <Form.Item
              label="Start year"
              name="start_year"
              className="font-semibold"
              rules={[
                { required: true, message: "Please Enter Start Year." },
              ]}>
              <Input className=" h-[40px]" placeholder="Enter Start Year" />
            </Form.Item>

            <Form.Item
              label="End Year"
              name="end_year"
              className="font-semibold"
              rules={[
                { required: true, message: "End year or Present." },
              ]}>
              <Input className=" h-[40px]" placeholder="End year or Present." />
            </Form.Item>


            <div className="flex gap-3 justify-end">
              <button
                onClick={() => {
                  handleCancel();
                  setIsModalEdit(false);
                }}
                className="button border p-2 rounded border-outline border-primary font-medium hover:bg-primary hover:text-tertiary ease-in-out">
                Cancel
              </button>
              <button
                type="submit"
                className="button border p-2 rounded border-outline border-primary bg-primary text-tertiary font-medium hover:bg-secondary hover:border-secondary hover:text-tertiary hover:font-semibold ease-in-out"
                disabled={form.isFieldsTouched(true) && form.getFieldsError().filter(({ errors }) => errors.length).length > 0}
              >
                {isModalEdit ? "Save Experence" : "Add Experence"}
              </button>

            </div>
          </Form>
        </Modal>
      </div>
    </>
  );
};

export default AdminExperience;
