import React, { useEffect, useState } from "react";
import { Form, Input, message } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { ReloadTrue, hideLoading, showLoading } from "../redux/rootSlice";
import axios from "axios";

const AdminSocial = () => {
    const { portfolioData } = useSelector((state) => state.root);
    const { intro } = portfolioData || {};
    const [selectItemEdit, setselectItemEdit] = useState(null);
    const [showedit, setshowedit] = useState(false);
    const dispatch = useDispatch();
    const [form] = Form.useForm();

    useEffect(() => {
        if (intro != null) {
            setshowedit(true);
        } else {
            setshowedit(false);
        }
    }, [intro]);


    const onFinish = async (values) => {
        try {
            let response
            dispatch(showLoading());
            if (selectItemEdit) {
                response = await axios.post(`https://backendz182.up.railway.app/api/portfolio/update-intro/${selectItemEdit?._id}`,
                    values
                );
            } else {
                response = await axios.post("https://backendz182.up.railway.app/api/portfolio/add-intro", values);
            }

            dispatch(hideLoading());

            if (response.data.success) {
                dispatch(ReloadTrue());
                message.success("Social Updated Succesfully");

            } else {
                message.error(response.data.message);
            }
        } catch (error) {
            console.error("Error:", error);
            message.error("An error occurred. Please try again.");
        }
    };

    return (
        <div>

            <div className="px-5 py-2 flex flex-col gap-3">
                <p className="text-xl font-bold">Add Social Links</p>
                <Form
                    form={form}
                    onFinish={onFinish}
                    layout="vertical"
                    initialValues={intro}
                    style={{ width: 550 }}
                    variant="filled"
                >
                    <Form.Item label="Facebook URL" name="facebook" className="font-semibold">
                        <Input className="h-[40px]" placeholder="Enter Facebook URL" />
                    </Form.Item>

                    <Form.Item label="Instagram URL" name="instagram" className="font-semibold">
                        <Input className="h-[40px]" placeholder="Enter Instagram URL" />
                    </Form.Item>

                    <Form.Item label="GitHub URL" name="github" className="font-semibold">
                        <Input className="h-[40px]" placeholder="Enter GitHub URL" />
                    </Form.Item>

                    <Form.Item label="Linkedin URL" name="linkdein" className="font-semibold">
                        <Input className="h-[40px]" placeholder="Enter Linkedin URL" />
                    </Form.Item>

                    <Form.Item label="WhatsApp URL" name="whatsapp" className="font-semibold">
                        <Input className="h-[40px]" placeholder="Enter WhatsApp URL" />
                    </Form.Item>

                    <Form.Item>
                        <button
                            className="h-[40px] float-right bg-transparent hover:bg-primary text-primary-700 font-semibold hover:text-white py-2 px-4 border border-primary hover:border-transparent rounded"
                            type="submit"
                            onClick={() => {
                                setselectItemEdit(intro)
                            }}
                        >
                            {showedit ? "Update" : "Add"}
                        </button>
                    </Form.Item>
                </Form>
            </div>
        </div>
    );
};

export default AdminSocial;
